@font-face {
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
  background-color: #ffffff;
}

.page__body a {
  text-decoration: none;
}

.page__body img {
  max-width: 100%;
  height: auto;
}

.page__body ul {
  list-style: none;
}

.header {
  position: relative;
  text-align: center;
  padding: 10px 0 0 0;
  margin-bottom: 2px;
}
@media (min-width: 1440px) {
  .header {
    padding-top: 10px;
  }
  .header--main {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }
  .header--main .nav__wrapper {
    background-color: transparent;
  }
  .header--main .nav__link {
    color: #ffffff;
  }
  .header--main .current {
    border-bottom: 2px solid #ffffff;
  }
}

@media (min-width: 768px) {
  .header__grid {
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
    justify-content: center;
  }
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 110px;
  }
}

.footer {
  padding: 30px 0 22px 0;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .footer {
    padding: 55px 0 61px 0;
  }
}
@media (min-width: 1440px) {
  .footer {
    padding: 57px 0;
  }
}

.footer__grid {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .footer__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}

.footer__logo {
  display: block;
  margin: 0 auto;
  margin-bottom: 25px;
}
.footer__logo:hover, .footer__logo:focus {
  opacity: 0.8;
}
.footer__logo:active {
  opacity: 0.6;
}
@media (max-width: 767px) {
  .footer__logo .footer__logo-text {
    width: 128px;
    height: 24px;
  }
}
@media (min-width: 768px) {
  .footer__logo {
    margin: 0;
  }
  .footer__logo .footer__logo-text {
    width: 128px;
    height: 24px;
  }
}
@media (min-width: 1440px) {
  .footer__logo {
    margin: 0;
  }
}

.footer__social {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  padding: 20px 0 17px 0;
  margin: 0 0 19px 0;
}
@media (max-width: 767px) {
  .footer__social {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .footer__social {
    padding: 0;
    margin: 0 0 0 30px;
    border: none;
  }
}
@media (min-width: 1440px) {
  .footer__social {
    padding: 0;
    margin: 0 0 0 33px;
    border: none;
  }
}

.footer__item {
  margin-right: 33px;
}
.footer__item:last-child {
  margin-right: 0;
}
@media (min-width: 768px) {
  .footer__item {
    margin-right: 25px;
  }
  .footer__item:nth-child(2) {
    margin-right: 22px;
  }
}

.footer__link {
  fill: #666666;
}
.footer__link:hover, .footer__link:focus {
  fill: #68b738;
}
.footer__link:active {
  fill: #68b738;
  opacity: 0.3;
}
.footer__link:focus .footer__fb {
  stroke: #68b738;
}
.footer__link:active .footer__fb {
  stroke: #68b738;
  opacity: 0.3;
}

@media (max-width: 767px) {
  .footer__vk {
    width: 22px;
    height: 22px;
  }
}
@media (min-width: 768px) {
  .footer__vk {
    width: 30px;
    height: 22px;
  }
}

.footer__instagram {
  width: 16px;
  height: 16px;
}
@media (max-width: 767px) {
  .footer__instagram {
    width: 22px;
    height: 22px;
  }
}
@media (min-width: 768px) {
  .footer__instagram {
    width: 23px;
    height: 23px;
  }
}

.footer__fb {
  fill: none;
  stroke: #666666;
}
.footer__fb:hover {
  stroke: #68b738;
}
@media (max-width: 767px) {
  .footer__fb {
    width: 22px;
    height: 22px;
  }
}
@media (min-width: 768px) {
  .footer__fb {
    width: 28px;
    height: 33px;
  }
}

.footer__dev {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__dev:hover .footer__academy-logo, .footer__dev:focus .footer__academy-logo {
  fill: #68b738;
}
.footer__dev:active, .footer__dev:active .footer__academy-logo {
  opacity: 0.3;
  fill: #68b738;
}
@media (max-width: 767px) {
  .footer__dev {
    width: 100%;
  }
}

.footer__academy-text {
  width: 100%;
  font-size: 16px;
  color: #444444;
  margin-right: 22px;
}

.footer__academy-logo {
  width: 27px;
  height: 34px;
  fill: #666666;
}

.button {
  display: block;
  min-width: 168px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: #68b738;
  border: none;
  cursor: pointer;
  padding: 10px;
}
.button:hover, .button:focus {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 1440px) {
  .button {
    min-width: 168px;
  }
}

.contact {
  position: relative;
  background-color: #f2f2f2;
}
.contact--main {
  background-color: #ffffff;
}
@media (min-width: 1440px) {
  .contact {
    overflow: hidden;
  }
}

.contact__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 23px 0 24px 0;
}
@media (min-width: 768px) {
  .contact__info {
    height: 170px;
    padding: 0 41px;
    z-index: 1;
  }
  .contact__info--catalog {
    height: 135px;
    padding-top: 37px;
    margin-bottom: 28px;
  }
  .contact__info--catalog .contact__title {
    padding-bottom: 5px;
  }
  .contact__info--catalog .contact__address {
    margin-left: 244px;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .contact__info {
    position: absolute;
    top: 24%;
    left: 7.5%;
    display: flex;
    height: auto;
    width: 500px;
    background-color: #ffffff;
    padding: 53px 36px;
  }
  .contact__info--catalog {
    padding-top: 60px;
    margin-bottom: 0;
  }
  .contact__info--catalog .contact__address {
    padding: 0 45px 0 0;
    margin-left: 22px;
  }
}

.contact__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  color: #111111;
  padding-top: 8px;
  margin: 0;
}
@media (min-width: 768px) {
  .contact__title {
    font-size: 20px;
    line-height: 24px;
    padding-top: 0;
    padding-bottom: 20px;
  }
}
@media (min-width: 1440px) {
  .contact__title {
    line-height: 25px;
    padding: 5px 0 0 42px;
  }
}

.contact__address {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-style: normal;
  text-align: left;
  padding: 5px 3px 0 0;
  margin-left: auto;
}
@media (min-width: 768px) {
  .contact__address {
    font-size: 16px;
    line-height: 20px;
    text-align: right;
    padding: 0 0 23px 0;
  }
}
@media (min-width: 1440px) {
  .contact__address {
    font-size: 16px;
    line-height: 20px;
    text-align: left;
    padding: 10px 53px 0 0;
  }
}

.contact__sity {
  display: block;
}
@media (min-width: 768px) {
  .contact__sity {
    margin-top: 20px;
  }
}

.contact__map {
  height: 374px;
}
@media (min-width: 768px) {
  .contact__map {
    height: 399px;
  }
}
@media (min-width: 1440px) {
  .contact__map {
    height: 400px;
  }
}

.contact__iframe {
  height: inherit;
  width: 100%;
}

.title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  text-transform: none;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}

.caption {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 26px;
  text-transform: none;
  color: #000000;
  padding: 0 23px;
}
@media (min-width: 768px) {
  .caption {
    font-size: 36px;
    line-height: 36px;
    padding: 0 38px 0 31px;
    margin-left: 30px;
  }
}
@media (min-width: 1440px) {
  .caption {
    padding: 0 40px;
    margin-left: 70px;
  }
}

.heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: #000000;
  margin: 0 0 36px 0;
}
@media (min-width: 768px) {
  .heading {
    font-size: 60px;
    line-height: 60px;
  }
}
@media (min-width: 1440px) {
  .heading {
    font-size: 60px;
    line-height: 60px;
    margin: 0 0 37px 0;
  }
}

.nav {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
}
@media (max-width: 767px) {
  .nav {
    font-weight: 500;
  }
  .nav--close .nav__wrapper {
    display: none;
  }
  .nav--close .nav__button {
    top: 32%;
    left: auto;
    right: 6%;
  }
  .nav--active {
    display: block;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 1;
  }
  .nav--active .nav__button {
    top: -22%;
    left: auto;
    right: 6%;
  }
  .nav--active .nav__button::before {
    transform: rotate(45deg);
  }
  .nav--active .nav__button::after {
    transform: rotate(-45deg);
  }
  .nav--active .nav__button-middle {
    display: none;
  }
  .nav--nojs .nav__wrapper {
    display: block;
  }
  .nav--nojs .nav__button {
    display: none;
  }
}
@media (min-width: 768px) {
  .nav {
    grid-column: 2/-1;
    grid-row: 1/-1;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .nav {
    font-size: 20px;
    line-height: auto;
  }
}

.nav__button {
  display: block;
  position: absolute;
  top: 7%;
  left: 85%;
  z-index: 2;
  width: 24px;
  height: 2px;
  padding: 10px 0;
  background: none;
  border: none;
  cursor: pointer;
}
.nav__button::before, .nav__button::after {
  content: "";
  position: absolute;
  right: 0%;
  width: 100%;
  height: 2px;
  background-color: #000000;
}
.nav__button::before {
  transform: translateY(-8px);
}
.nav__button::after {
  transform: translateY(6px);
}
@media (min-width: 768px) {
  .nav__button {
    display: none;
  }
}

.nav__button-middle {
  display: block;
  top: 50%;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #000000;
}

.nav__wrapper {
  background-color: #ffffff;
}

.nav__list {
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
