.product {
  font-family: "Oswald", "Arial", sans-serif;
  margin-bottom: 27px;
}
@media (min-width: 768px) {
  .product {
    margin-bottom: 45px;
  }
}
@media (min-width: 1440px) {
  .product {
    margin-bottom: 71px;
  }
}

.product__list {
  padding: 0;
  margin: 0;
}
@media (min-width: 1440px) {
  .product__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 80px;
  }
}

.product__item {
  position: relative;
  padding: 30px 20px 12px;
  background-color: #f2f2f2;
  margin-bottom: 20px;
}
.product__item:last-child {
  margin-bottom: 0;
}
.product__item::before, .product__item::after {
  position: absolute;
  content: "";
}
.product__item::before {
  top: 22px;
  left: 22px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #68b738;
}
.product__item:nth-child(1)::after {
  top: 19px;
  left: 28px;
  width: 37px;
  height: 50px;
  background-image: url("../../../img/catalog/icon/cat-slim.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}
.product__item:nth-child(2)::after {
  top: 34px;
  left: 13px;
  width: 67px;
  height: 28px;
  background-image: url("../../../img/catalog/icon/cat-pro.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}
@media (min-width: 768px) {
  .product__item {
    padding: 77px 44% 64px 53px;
    margin-bottom: 30px;
  }
  .product__item:last-child {
    margin-bottom: 0;
  }
  .product__item::before {
    top: 91px;
    left: auto;
    right: 61px;
    width: 200px;
    height: 200px;
  }
  .product__item:nth-child(1)::after {
    top: 80px;
